.editCardContainer {
  font-size: 1.4rem;
  font-weight: bold;
  border: 2px solid var(--lightGray);
  border-radius: 1.2rem;
  background-color: #f5f5f5;
  margin-bottom: 1.5rem;
}

.editCardHeader {
  display: flex;
  padding: 1.2rem;
  justify-content: space-between;
  border-top-left-radius: 1.2rem;
  border-top-right-radius: 1.2rem;
}

.editCardHeader i {
  color: var(--darkGray);
}

.editCardFooter {
  display: flex;
  padding: 0.9rem;
  justify-content: space-between;
  border-bottom-left-radius: 1.2rem;
  border-bottom-right-radius: 1.2rem;
  background-color: #eeeeee;
  font-size: 1.2rem;
}

.editCardFooter span,
.editCardHeader span {
  color: var(--darkGray);
  font-weight: 600;
}

.updateDonorBtn {
  color: var(--white);
  font-size: 1.6rem;
  background-color: var(--primary);
  width: 100%;
  height: 4.2rem;
  border: none;
  border-radius: 1.2rem;
  margin: 1.6rem 0;
}
