.donor-details-wrapper-1 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.donor-details-container-1 {
  width: 90vw;
  margin: 0 auto;
}

.donor-header-1 {
  margin-top: 2em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.donor-details-1 > .inputField {
  margin: 1.1em 0;
}

.donor-details-1 > p,
.donor-details-1 > .inputField > p {
  color: rgb(240, 69, 69);
  margin-left: 0.5em;
  margin-top: 0.2em;
  font-size: 1.1rem;
}

.donor-details-1 > .inputField > input {
  display: block;
  width: 100%;
}

.donor-details-1 > .inputField > input[type="text"],
input[type="number"],
input[type="date"] {
  height: 39px;
  border: solid 2px var(--placeholder);
  border-radius: 12px;
  padding-left: 1em;
  background-color: var(--white);
}

.donor-details-1 > .inputField > input[type="text"]::placeholder {
  color: var(--placeholder);
}

.donor-details-1 > .inputField input[type="text"],
input[type="number"],
input[type="date"]:focus {
  outline: none;
}

.donor-details-1 > select {
  height: 39px;
  border: solid 2px var(--placeholder);
  border-radius: 12px;
  padding-left: 0.5em;
  font-size: 1.5rem;
  width: 100%;
  font-size: 1.6rem;
  background-color: var(--white);
}

.donor-details-1 > select:invalid {
  color: var(--placeholder);
}

.donor-header-1 > h2 > img {
  width: 8px;
  margin-right: 0.5em;
}
.donor-header-1 > h2 {
  font-size: 1.7rem;
}
.donor-header-1 > p {
  font-size: 1.4rem;
  color: var(--darkGray);
  font-weight: 500;
}

.reciept-button-1 {
  width: 90%;
  margin: 0 auto 1.5rem;
  flex: 0.8;
}

.reciept-button-1 > button {
  border: none;
  outline: none;
  border-radius: 12px;
  background-color: var(--primary);
  height: 42px;
  width: 100%;
  color: var(--white);
  font-size: 1.6rem;
  position: relative;
}
.reciept-button-1 > button > span {
  position: absolute;
  right: 30px;
}

.spinner {
  border: solid 2px var(--placeholder);
  border-radius: 12px;
  height: 39px;
  margin-top: 1.2em;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.spinner > input[type="number"] {
  height: 100%;
  width: 80%;
  padding-left: 1em;
  outline: none;
  border: none;
}

.spinner > input[type="number"]::placeholder {
  color: var(--placeholder);
}

.radioButtons {
  display: flex;
  margin-top: 1em;
}

.radioButtons label {
  margin-left: 0.25em;
  font-weight: 500;
}

.radioButtons input {
  width: 15px;
  height: 15px;
}

.male {
  display: flex;
  align-items: center;
}

.male > label {
  font-size: 1.4rem;
}

.female {
  display: flex;
  align-items: center;
  margin-left: 1em;
}

.female > label {
  font-size: 1.4rem;
}

.address-details > .inputField {
  margin: 1.1em 0;
}

.address-details > .inputField > p {
  color: rgb(240, 69, 69);
  margin-left: 0.5em;
  font-size: 1.1rem;
}

.address-details > .inputField > input {
  display: block;
  width: 100%;
}

.address-details > h2 {
  margin-top: 1.4em;
  font-size: 1.7rem;
}

.address-details > .inputField > input[type="text"],
input[type="number"] {
  height: 39px;
  border: solid 2px var(--placeholder);
  border-radius: 12px;
  padding-left: 1em;
}

.address-details > .inputField > input::placeholder {
  color: var(--placeholder);
}

.address-details > .inputField > input[type="text"]:focus {
  outline: none;
}

.address-details > .inputField > select {
  height: 39px;
  border: solid 2px var(--placeholder);
  border-radius: 12px;
  padding-left: 0.5em;
  font-size: 1.5rem;
  width: 100%;
  font-size: 1.6rem;
  background-color: var(--white);
}

.address-details > .inputField > select:invalid {
  color: var(--placeholder);
}

.donorList {
  border: solid 3px red;
  list-style-type: none;
}

.donor-dropdown {
  max-height: 260px;
  display: none;
  position: absolute;
  background-color: #f6f6f6;
  min-width: 230px;
  width: 90%;
  padding: 4px 16px;
  border-radius: 10px;
  box-shadow: 0 0 15px 6px #d1d1d1;
  overflow: auto;
  z-index: 1;
}

.donor-dropdown label {
  border-bottom: solid 1px silver;
  font-size: 1.5em;
  color: black;
  margin: 0.6em 0px;
  padding-bottom: 0.8rem;
  text-decoration: none;
  display: block;
}

.donor-dropdown label p:first-child {
  font-weight: 500;
}

.donor-dropdown label p:last-child,
.donor-dropdown label p:nth-child(2) {
  font-size: 1.25rem;
  margin-top: 0.4rem;
}

.donor-dropdown label p i {
  color: var(--darkGray);
  margin-right: 5px;
}

.donor-dropdown input {
  display: none;
}

.donor-dropdown label:hover {
  background-color: #ddd;
}

.donor-show {
  display: block;
}

.dropdownContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.dropdownContainer div:nth-child(1) {
  width: 100%;
}

.dropdownContainer div:nth-child(2) i {
  font-size: 1.8rem;
  color: var(--darkGray);
}
